	module.exports = {"data":{"markdownRemark":{"html":"<p>Public disclosure enables programs to be transparent about the security vulnerabilities found for their program. HackerOne's public disclosure process balances transparency with control over what information is shared with the public.  </p>\n<p>Programs can choose from 3 disclosure settings:</p>\n<table>\n<thead>\n<tr>\n<th>Option</th>\n<th>Detail</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Disclosure by Default</td>\n<td>You or the security team can request public disclosure for any closed report in the program. If the admin of the program agrees to disclosure, the contents of the report will be made public within 30 days.\n<br>\n \n<br>\n<em>This is the default setting for all verified programs</em>\n.</td>\n</tr>\n<tr>\n<td>Disclosure requiring Mutual Agreement</td>\n<td>You can request public disclosure for any closed report in the program. If the program security team agrees to disclosure, the contents of the report will be made public. If the security team doesn't take any action, the contents of the report will remain private. \n<br>\n<em>The program must request to opt-in to this option.</em></td>\n</tr>\n<tr>\n<td>Disclosure Disabled</td>\n<td>Public disclosure isn't allowed for any report.</td>\n</tr>\n</tbody>\n</table>\n<h3>Requesting Public Disclosure</h3>\n<p>Both you and program members can request public disclosure. To request public disclosure:</p>\n<ol>\n<li>Go to the report you want to publicly disclose.</li>\n<li>Make sure the report is closed.</li>\n<li>Select <strong>Request public disclosure</strong> in the action picker at the bottom of the report.</li>\n<li>Select whether you want to disclose the <strong>Full</strong> report or <strong>Limited</strong>.</li>\n</ol>\n<table>\n<thead>\n<tr>\n<th>Option</th>\n<th>Details</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Full</td>\n<td>Upon public disclosure, the full contents of the report are visible including:\n<ul>\nVulnerability information\n</ul>\n<ul>\nSummary\n</ul>\n<ul>\nTimeline such as comments and attachment\n</ul>\n<br>\n<em>Note: Internal comments are hidden.</em></td>\n</tr>\n<tr>\n<td>Limited</td>\n<td>Only the summary and timeline of the activity are visible. All comments and attachments are hidden. Limited disclosure allows for greater control over sensitive or extraneous information.</td>\n</tr>\n</tbody>\n</table>\n<ol start=\"5\">\n<li><em>(Optional)</em> Enter a comment to describe your reasons for public disclosure.</li>\n<li>Click <strong>Post</strong>.    </li>\n</ol>\n<p><img src=\"./images/public-disclosure-2.png\" alt=\"public-disclosure-2\"></p>\n<p>After public disclosure has been requested, the admin of the of the program can choose to publicly disclose the report. They can select <strong>Disclose publicly</strong> to disclose the report.</p>\n<p><img src=\"./images/public-disclosure-3.png\" alt=\"public-disclosure-3\"></p>\n<p>When publishing reports, the security team can choose to disclose the report in full or limit the information published. The default is to display all the communication between the hacker and the security team from first report to resolution. </p>\n<p>For more information, please read the full <a href=\"https://hackerone.com/disclosure-guidelines\">HackerOne Disclosure Guidelines</a>.  There is a lengthier blog post on <a href=\"https://hackerone.com/blog/public-disclosure-on-hackerone?utm_source=h1&#x26;utm_medium=help&#x26;utm_campaign=hdpdw\">Public Disclosure</a> as well.  If disclosure was accidentally initiated or you have concerns about this process, please <a href=\"https://support.hackerone.com/hc/en-us/requests/new\">submit a support request</a>.</p>","frontmatter":{"title":"Public Disclosure","id":"hackers/public-disclosure","path":"/hackers/public-disclosure.html"}}},"pathContext":{}}

